import React, { useState } from "react";
import logo from "./logo.svg";
import "./App.css";

import RandomImg from "./media/acak.png";
import Batu from "./media/batu.png";
import Gunting from "./media/gunting.png";
import Kertas from "./media/kertas.png";
import SoundEffect from "./media/game.wav";

function App() {
  const [player1, setPlayer1] = useState("");
  const [player2, setPlayer2] = useState("");

  const [err, setErr] = useState(false);

  const [statusGame, setStatusGame] = useState("");

  const [scoreP1, setScoreP1] = useState(0);
  const [scoreP2, setScoreP2] = useState(0);

  const [winner, setWinner] = useState("");

  const [audio] = useState(new Audio(SoundEffect));

  const options = [
    {
      name: "batu",
      img: Batu,
    },
    {
      name: "gunting",
      img: Gunting,
    },
    {
      name: "kertas",
      img: Kertas,
    },
  ];

  React.useEffect(() => {
    if (player2) {
      setErr(false);
    }
  }, [player2]);

  const startGame = () => {
    if (player2) {
      setStatusGame("play");
      audio.play();
      setWinner("");
      const acakGambar = setInterval(() => {
        setPlayer1(randomAngka());
      }, 50);

      const delayGame = setInterval(() => {
        clearInterval(acakGambar);
        clearInterval(delayGame);
        setStatusGame("end");
      }, 3000);
    } else {
      setErr(true);
      console.log("player2 belum memilih");
    }
  };

  const randomAngka = () => {
    let randomX = Math.floor(Math.random() * 3);
    return options[randomX];
  };

  const cekWinner = () => {
    console.log("player1", player1.name);
    console.log("player2", player2);
    if (player1.name === player2) {
      console.log("draw");
      setWinner("DRAW");
    } else if (player1.name === "batu" && player2 === "kertas") {
      console.log("player2 win");
      setWinner("YOU WIN");
      setScoreP2(scoreP2 + 1);
    } else if (player1.name === "kertas" && player2 === "gunting") {
      console.log("player2 win");
      setWinner("YOU WIN");
      setScoreP2(scoreP2 + 1);
    } else if (player1.name === "gunting" && player2 === "batu") {
      console.log("palyer2 win");
      setWinner("YOU WIN");
      setScoreP2(scoreP2 + 1);
    } else {
      console.log("player1 win");
      setWinner("YOU LOSE");
      setScoreP1(scoreP1 + 1);
    }
  };

  React.useEffect(() => {
    if (statusGame === "end") {
      cekWinner();
    }
    // eslint-disable-next-line
  }, [statusGame]);

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <div className="wrapper-game">
          <div>{winner ? winner : "BATU GUNTING KERTAS"}</div>
          <div className="d-flex justify-content-center">
            <div className="card-img">
              <img src={player1 ? player1.img : RandomImg} alt="gambar-acak" />
            </div>
          </div>
          <div className="d-flex justify-content-between align-item-center">
            <div className="card-score">
              <div className="">PC</div>
              <div className="">{scoreP1}</div>
            </div>
            <div className="start-button" onClick={() => startGame()}>
              START
            </div>
            <div className="card-score">
              <div className="">YOU</div>
              <div className="">{scoreP2}</div>
            </div>
          </div>
          <div
            className="d-flex justify-content-center"
            style={{ marginTop: "32px" }}
          >
            {options.map((item, index) => (
              <div
                className={
                  player2 === item.name
                    ? "card-img icon-game active"
                    : "card-img icon-game"
                }
                key={index}
              >
                <img
                  src={item.img}
                  alt={item.name}
                  onClick={() => setPlayer2(item.name)}
                />
              </div>
            ))}
          </div>

          <div
            className="d-flex text-center"
            style={err ? { opacity: 1 } : { opacity: 0 }}
          >
            Yuk tentukan pilihan dulu!!! mau "BATU", "GUNTING" atau "KERTAS"
          </div>
        </div>
      </header>
    </div>
  );
}

export default App;
